import { Command} from "../../extensions/Command";
import { EmbedBuilder } from "discord.js";
import ms from "ms";

export default class pingCommand implements Command{
    name: "ping";
    description: "Shows the bot's ping";
    async run({ interaction, client }) {
        const embed = new EmbedBuilder()
            .setAuthor({ name: interaction.user.tag, iconURL: interaction.user.displayAvatarURL() })
            .addFields(
                {
                    name: "Bot Information",
                    value: `・ Client: ${client.ws.ping}ms\n ・ API: ${Date.now() - interaction.createdTimestamp}ms\n ・ Uptime: ${ms(Math.floor(client.uptime || 0 / 1000))}\n・ Platform: ${process.platform}\n ・ Arch: ${process.arch}\n ・ Node: ${process.version}\n ・ Discord.js: ${require("discord.js").version}`,
                }
            )
            .setColor(`#af42d7`)
            interaction.reply({embeds: [embed]})
    }
}